import React from "react";
import FullCalendar from "@fullcalendar/react";
import timeGridPlugin from "@fullcalendar/timegrid";
const HomeScheduler = () => {
  const events = {
    events: [
      {
        id: 0,
        title: "Cheak-In",

        start: "2022-03-29T14:00:00.000Z",

        end: "2022-03-29T14:00:00.000Z",
      },

      {
        id: 2,
        title: "Cheak-In",
        start: "2022-03-29T116:00:00.000Z",

        end: "2022-03-29T16:00:00.000Z",
      },
    ],
  };
  return (
    <FullCalendar
      plugins={[timeGridPlugin]}
      initialView="timeGridWeek"
      allDaySlot={false}
      events={events}
      dayHeaders={false}
      customButtons={false}
      selectable={"true"}
      editable={true}
      selectAllow={true}
      titleFormat={""}
      slotLabelFormat={{ hour: "numeric", minute: "2-digit" }}
    />
  );
};
export default HomeScheduler;
